/*
  The outer-most container in a typical Jcrop instance
  If you are having difficulty with formatting related to styles
  on a parent element, place any fixes here or in a like selector

  You can also style this element if you want to add a border, etc
  A better method for styling can be seen below with .jcrop-light
  (Add a class to the holder and style elements for that extended class)
*/
.jcrop-holder {
    direction: ltr;
    text-align: left;
}

/* These styles define the border lines */

.jcrop-vline, .jcrop-hline {
    background: #FFF url('data:image/gif;base64,') /*/content/images/Jcrop.gif*/ top left repeat;
    font-size: 0;
    position: absolute;
}

.jcrop-vline {
    height: 100%;
    width: 1px !important;
}

.jcrop-hline {
    height: 1px !important;
    width: 100%;
}

.jcrop-vline.right { right: 0; }

.jcrop-hline.bottom { bottom: 0; }

/* Handle style - size is set by Jcrop handleSize option (currently) */

.jcrop-handle {
    background-color: #333;
    border: 1px #eee solid;
    font-size: 1px;
}

/* This style is used for invisible click targets */

.jcrop-tracker {
    -webkit-tap-highlight-color: transparent; /* "turn off" link highlight */
    -webkit-touch-callout: none; /* disable callout, image save panel */
    -webkit-user-select: none; /* disable cut copy paste */
    height: 100%;
    width: 100%;
}

/* Positioning of handles and drag bars */

.jcrop-handle.ord-n {
    left: 50%;
    margin-left: -4px;
    margin-top: -4px;
    top: 0;
}

.jcrop-handle.ord-s {
    bottom: 0;
    left: 50%;
    margin-bottom: -4px;
    margin-left: -4px;
}

.jcrop-handle.ord-e {
    margin-right: -4px;
    margin-top: -4px;
    right: 0;
    top: 50%;
}

.jcrop-handle.ord-w {
    left: 0;
    margin-left: -4px;
    margin-top: -4px;
    top: 50%;
}

.jcrop-handle.ord-nw {
    left: 0;
    margin-left: -4px;
    margin-top: -4px;
    top: 0;
}

.jcrop-handle.ord-ne {
    margin-right: -4px;
    margin-top: -4px;
    right: 0;
    top: 0;
}

.jcrop-handle.ord-se {
    bottom: 0;
    margin-bottom: -4px;
    margin-right: -4px;
    right: 0;
}

.jcrop-handle.ord-sw {
    bottom: 0;
    left: 0;
    margin-bottom: -4px;
    margin-left: -4px;
}

.jcrop-dragbar.ord-n, .jcrop-dragbar.ord-s {
    height: 7px;
    width: 100%;
}

.jcrop-dragbar.ord-e, .jcrop-dragbar.ord-w {
    height: 100%;
    width: 7px;
}

.jcrop-dragbar.ord-n { margin-top: -4px; }

.jcrop-dragbar.ord-s {
    bottom: 0;
    margin-bottom: -4px;
}

.jcrop-dragbar.ord-e {
    margin-right: -4px;
    right: 0;
}

.jcrop-dragbar.ord-w { margin-left: -4px; }

/* The "jcrop-light" class/extension */

.jcrop-light .jcrop-vline, .jcrop-light .jcrop-hline {
    background: #FFF;
    -webkit-filter: Alpha(opacity=70) !important;
    -moz-filter: Alpha(opacity=70) !important;
    -o-filter: Alpha(opacity=70) !important;
    filter: Alpha(opacity=70) !important;
    -ms-opacity: .70 !important;
    opacity: .70 !important;
}

.jcrop-light .jcrop-handle {
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    background-color: #000;
    border-color: #FFF;
    -ms-border-radius: 3px;
    border-radius: 3px;
}

/* The "jcrop-dark" class/extension */

.jcrop-dark .jcrop-vline, .jcrop-dark .jcrop-hline {
    background: #000;
    -webkit-filter: Alpha(opacity=70) !important;
    -moz-filter: Alpha(opacity=70) !important;
    -o-filter: Alpha(opacity=70) !important;
    filter: Alpha(opacity=70) !important;
    -ms-opacity: .7 !important;
    opacity: .7 !important;
}

.jcrop-dark .jcrop-handle {
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    background-color: #FFF;
    border-color: #000;
    -ms-border-radius: 3px;
    border-radius: 3px;
}

/* Fix for twitter bootstrap et al. */

.jcrop-holder img, img.jcrop-preview { max-width: none; }