.chosen-container {
    -moz-user-select: none;
    -webkit-user-select: none;
    display: inline-block;
    *display: inline;
    font-size: 13px;
    position: relative;
    user-select: none;
    vertical-align: middle;
    zoom: 1;
}

.chosen-container .chosen-drop {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background: #fff;
    border: 1px solid #aaa;
    border-top: 0;
    box-shadow: 0 4px 5px rgba(0, 0, 0, .15);
    box-sizing: border-box;
    left: -9999px;
    position: absolute;
    top: 100%;
    width: 100%;
    z-index: 1010;
}

.chosen-container.chosen-with-drop .chosen-drop { left: 0; }

.chosen-container a { cursor: pointer; }

.chosen-container-single .chosen-single {
    background: -webkit-gradient(linear, 50% 0, 50% 100%, color-stop(20%, #fff), color-stop(50%, #f6f6f6), color-stop(52%, #eee), color-stop(100%, #f4f4f4));
    background: -webkit-linear-gradient(top, #fff 20%, #f6f6f6 50%, #eee 52%, #f4f4f4 100%);
    background: -moz-linear-gradient(top, #fff 20%, #f6f6f6 50%, #eee 52%, #f4f4f4 100%);
    background: -o-linear-gradient(top, #fff 20%, #f6f6f6 50%, #eee 52%, #f4f4f4 100%);
    background: linear-gradient(top, #fff 20%, #f6f6f6 50%, #eee 52%, #f4f4f4 100%);
    background-clip: padding-box;
    background-color: #fff;
    border: 1px solid #aaa;
    border-radius: 5px;
    box-shadow: 0 0 3px #fff inset, 0 1px 1px rgba(0, 0, 0, .1);
    color: #444;
    display: block;
    height: 23px;
    line-height: 24px;
    overflow: hidden;
    padding: 0 0 0 8px;
    position: relative;
    text-decoration: none;
    white-space: nowrap;
}

.chosen-container-single .chosen-default { color: #999; }

.chosen-container-single .chosen-single span {
    display: block;
    margin-right: 26px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.chosen-container-single .chosen-single-with-deselect span { margin-right: 38px; }

.chosen-container-single .chosen-single abbr {
    background: /*url('../images/chosen-sprite.png')*/ -42px 1px no-repeat;
    display: block;
    font-size: 1px;
    height: 12px;
    position: absolute;
    right: 26px;
    top: 6px;
    width: 12px;
}

.chosen-container-single .chosen-single abbr:hover { background-position: -42px -10px; }

.chosen-container-single.chosen-disabled .chosen-single abbr:hover { background-position: -42px -10px; }

.chosen-container-single .chosen-single div {
    display: block;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    width: 18px;
}

.chosen-container-single .chosen-single div b {
    background: /* url('../images/chosen-sprite.png')*/ no-repeat 0 2px;
    display: block;
    height: 100%;
    width: 100%;
}

.chosen-container-single .chosen-search {
    margin: 0;
    padding: 3px 4px;
    position: relative;
    white-space: nowrap;
    z-index: 1010;
}

.chosen-container-single .chosen-search input[type=text] {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background: #fff/*url('../images/chosen-sprite.png')*/ no-repeat 100% -20px;
    background: /* url('../images/chosen-sprite.png')*/ no-repeat 100% -20px, -webkit-gradient(linear, 50% 0, 50% 100%, color-stop(1%, #eee), color-stop(15%, #fff));
    background: /* url('../images/chosen-sprite.png')*/ no-repeat 100% -20px, -webkit-linear-gradient(#eee 1%, #fff 15%);
    background: /*url('../images/chosen-sprite.png')*/ no-repeat 100% -20px, -moz-linear-gradient(#eee 1%, #fff 15%);
    background: /*url('../images/chosen-sprite.png')*/ no-repeat 100% -20px, -o-linear-gradient(#eee 1%, #fff 15%);
    background: /*url('../images/chosen-sprite.png')*/ no-repeat 100% -20px, linear-gradient(#eee 1%, #fff 15%);
    border: 1px solid #aaa;
    border-radius: 0;
    box-sizing: border-box;
    font-size: 1em;
    height: auto;
    line-height: normal;
    margin: 1px 0;
    outline: 0;
    padding: 4px 20px 4px 5px;
    width: 100%;
}

.chosen-container-single .chosen-drop {
    background-clip: padding-box;
    border-radius: 0 0 4px 4px;
    margin-top: -1px;
}

.chosen-container-single.chosen-container-single-nosearch .chosen-search {
    left: -9999px;
    position: absolute;
}

.chosen-container .chosen-results {
    -webkit-overflow-scrolling: touch;
    margin: 0 4px 4px 0;
    max-height: 240px;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 0 0 0 4px;
    position: relative;
}

.chosen-container .chosen-results li {
    display: none;
    line-height: 15px;
    list-style: none;
    margin: 0;
    padding: 5px 6px;
}

.chosen-container .chosen-results li.active-result {
    cursor: pointer;
    display: list-item;
}

.chosen-container .chosen-results li.disabled-result {
    color: #ccc;
    cursor: default;
    display: list-item;
}

.chosen-container .chosen-results li.highlighted {
    background-color: #3875d7;
    background-image: -webkit-gradient(linear, 50% 0, 50% 100%, color-stop(20%, #3875d7), color-stop(90%, #2a62bc));
    background-image: -webkit-linear-gradient(#3875d7 20%, #2a62bc 90%);
    background-image: -moz-linear-gradient(#3875d7 20%, #2a62bc 90%);
    background-image: -o-linear-gradient(#3875d7 20%, #2a62bc 90%);
    background-image: linear-gradient(#3875d7 20%, #2a62bc 90%);
    color: #fff;
}

.chosen-container .chosen-results li.no-results {
    background: #f4f4f4;
    display: list-item;
}

.chosen-container .chosen-results li.group-result {
    cursor: default;
    display: list-item;
    font-weight: 700;
}

.chosen-container .chosen-results li.group-option { padding-left: 15px; }

.chosen-container .chosen-results li em {
    font-style: normal;
    text-decoration: underline;
}

.chosen-container-multi .chosen-choices {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background-color: #fff;
    background-image: -webkit-gradient(linear, 50% 0, 50% 100%, color-stop(1%, #eee), color-stop(15%, #fff));
    background-image: -webkit-linear-gradient(#eee 1%, #fff 15%);
    background-image: -moz-linear-gradient(#eee 1%, #fff 15%);
    background-image: -o-linear-gradient(#eee 1%, #fff 15%);
    background-image: linear-gradient(#eee 1%, #fff 15%);
