
html {
    min-height: 100%;
    position: relative;
}

body {
    background-color: #e4e6e9;
    min-height: 100%;
    padding-bottom: 0;
    font-size: 14px;
    color: #393939;
    line-height: 1.5;
}

.main-container:before {
    display: block;
    content: "";
    position: absolute;
    z-index: -2;
    width: 100%;
    max-width: inherit;
    bottom: 0;
    top: 0;
    background-color: #FFF;
}

.page-operations {
    margin-bottom: 10px;
}

.margin-bottom-10 {
    margin-bottom: 10px;
}

#busy-holder {
    background: #FFF;
    display: none;
    filter: alpha(opacity=80);
    height: auto !important;
    height: 100%;
    min-height: 100%;
    opacity: .8;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 10001;
}

#busy {
    height: 128px;
    left: 50%;
    position: fixed;
    top: 30%;
    width: 128px;
    z-index: 10000;
}

.main-container.container, .rtl .main-container.container {
    padding-left: 0;
    padding-right: 0;
}

    .main-container.container:before {
        -webkit-box-shadow: 0 0 0 1px rgba(0,0,0,.1);
        box-shadow: 0 0 0 1px rgba(0,0,0,.1);
        width: inherit;
    }

@media (max-width:767px) {
    .main-container.container:before {
        -webkit-box-shadow: none;
        box-shadow: none;
        width: 100%;
    }
}

.main-content {
    margin-left: 0;
    min-height: 100%;
    padding: 0;
}

.sidebar + .main-content {
